function getOneInNameArray(arrays) {
  var index = Math.floor((Math.random() * arrays.length))
  return arrays[index]
}

function makeBoy() {
  var first = getOneInNameArray(xing)
  var second = ""

  // 
  var choice = Math.random() < 0.7 ? 1 : 2
  switch (choice) {
    case 1:
      second = getOneInNameArray(boy2)
      break;
    case 2:
      second = getOneInNameArray(boy1)
      break;
  }
  return first + second
}

function makeGirl() {
  var first = getOneInNameArray(xing)
  var second = ""

  // 
  var choice = Math.random() < 0.7 ? 1 : 2
  switch (choice) {
    case 1:
      second = getOneInNameArray(girl2)
      break;
    case 2:
      second = getOneInNameArray(girl1)
      break;
  }
  return first + second
}

function generate(num, sex) {
  var i = 0;
  var str = "";

  for (; i < num; i++) {
    if (sex == "boy") {
      str = str + makeBoy() + "\n";
    }
    if (sex == "girl") {
      str = str + makeGirl() + "\n";
    }
    if (sex == "all") {
      var choice = Math.floor((Math.random() * 2) + 1)
      switch (choice) {
        case 1:
          str = str + makeBoy() + "\n";
          break;
        case 2:
          str = str + makeGirl() + "\n";
          break;
      }
    }
  }

  return str
}

export default generate





